.header {
    position: absolute;
    right: 0;
    left: 0;
    top: 0;
    z-index: 10;
    display: flex;
    justify-content: center;
    padding: 10px;
}

.headerNav {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.headerBrand {
    display: flex;
}

.headerBrand svg {
    width: 26px;
    height: 26px;
    padding: 0 5px;
    fill: var(--sw-color-brand);
}

.headerMenu {
    display: flex;
    padding: 0 120px;
}

.headerMenu button {
    background-color: transparent;
    border: none;
    color: var(--sw-color-white);
    font-family: sans-serif;
    font-weight: bold;
    text-transform: capitalize;
}
