.App {
    margin: 0;
    padding: 0;
    background-color: transparent;
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    right: 0;
    height: 100vh;
    overflow: hidden;
}

::selection {
    background-color: var(--sw-color-white);
    color: var(--sw-color-black)
}

/*
